.table_table {
    margin: auto;
    margin-bottom: 50px;
}

.table_thead_tr {
    border-bottom: 4px solid #ffc107;
}

.table_tbody_tr {
    border-bottom: 1px solid #ffc107;
}

.table_thead_th {
    padding: 10px;
}

.table_tbody_td {
    padding: 10px;
}

.hero_bg {
  background-color: rgba(0,0,0,0.8);
  padding: 22% 0% 12.5% 0%;
  border-bottom: 30px solid #ffc107;
  height: 890px;
}

.videoBg {
  position: absolute;
  z-index: -1; top: 0px;
  left: 0px;
  height: 890px;
  width: 98.8vw;
  opacity: 1;
  overflow: hidden;
}

.videoBg video {
  position: absolute;
  z-index: -1;
  top: 0px;
  left:0px;
  min-width: 100%;
  min-height: 100%;
}

.hero {
  width: 50%;
  margin: 0% auto 0px auto;
}

.hero_img {
  width: 190px;
  max-width: inherit !important;
}

.hero_logo {
  width: 25%;
  vertical-align: top;
}

.hero_title {
  width: 75%;
  padding: 0px 0px 0px 10px;
  vertical-align: top;
}

.hero_h1 {
  font-size: 113px;
  font-weight: bold;
  margin: 0px;
}

.hero_h2 {
  font-size: 28px;
  margin: 0px;
  text-align: center;
}

.hero_h3 {
  font-size: 28px;
  margin: 0px;
  padding-left: 5px;
  text-align: center;
}

/* Small devices (landscape phones, 576px and up) */
@media (max-width: 576px) { 
 
  .hero_bg {
    height: 300px;
  }

  .videoBg {
    height: 300px;
  }

}
.btn {
    text-transform: capitalize;
}

.section_1, .section_2 {
    padding: 25px 0px;
    text-align: center;
}

.section_1 {
    margin-top: 100px;
}

.section_2 {
    margin-top: 50px;
}

.section_1 p, .section_2 p {
    font-size: 24px;
}

.section_h1 {
    border-bottom: 4px solid #ffc107;
    display: inline-block;
    margin-bottom: 20px;
}

.section_h2 {
    border-bottom: 4px solid #ffc107;
    display: inline-block;
    margin-bottom: 20px;
}

.card {
    border-radius: 16px;
    background: #17141d;
    box-shadow: -1rem 0 3rem #000;
    margin-bottom: 50px;
}

.card h3 {
    margin-bottom: 0;
}

.price {
    font-size: 50px;
    font-weight: 400;
    color: #ffc107;
}

.card-footer p, .card-body p {
    font-size: 14px !important;
}

.card-body ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.card-footer {
    border-top: 1px solid rgba(255, 193, 7, 0.125);
}

.card-header {
    border-bottom: 1px solid rgba(255, 193, 7, 0.125);
}

.speaker img {
    border-radius: 50%;
    width: 70%;
    margin-bottom: 20px;
}

.speaker p {
    margin: 0px;
}

body {
  background-color: #100e17;
  font-family: 'Josefin Sans', sans-serif;
  color: #ffffff;
}

a {
  color: #ffc107;
}

a:hover {
  color: #ffc107;
}

.content {
  padding-top: 100px;
}

.footer {
  padding: 5% 0%;
  background-color: #ffc107;
  margin-top: 150px;
}

.navbar-dark .navbar-nav .nav-link {
  color: rgba(255,255,255, 1);
}

.footer p {
  margin-bottom: 0px;
  font-size: 20px;
  padding-right: 10%;
  margin-bottom: 10px;
}

.footer i {
  font-size: 35px;
}

.footer a {
  color: #ffffff;
  font-size: 20px
}

.footer h1 {
  font-weight: 700;
  font-size: 22px;
  margin: 0 0 .8rem 0
}

.mail-list {
  color: #ffffff;
  background: #e8ae00;
  clear: left;
  font: 14px Helvetica, Arial,sans-serif;
  width:100%;
  margin-top: 200px;
  padding: 2% 0%;
}

.presentingSponsor {
  height: 250px;
  margin-bottom: 30px;
}

.diamondSponsor {
  height: 250px;
}

iframe {
  border: none;
}

img {
  max-width: 100%;
}

.iconroup button {
  margin-left: 5px;
  border-radius: 50%;
  padding-top: 12px;
  border: none;
}

.btnGroup button {
  margin-left: 5px;
}

.btn-outline-light:hover {
  color: #ffc107;
  background-color: #100e17;
  border-color: #100e17;
}

.email {
  height: 70px;
  max-height: 70px;
  border-radius: 80px;
  padding-left: 25px;
  font-size: 20px;
  font-weight: 500;
  width: 400px;
  margin-bottom: 30px;
  border: none;
  max-width: 100%;
}

.email:focus {
  outline: none;
}

.pageBody {
  min-height: 900px;
}

.blog_entry_preview_title {
  color: #ffc107;
}

.hide {
  display: none;
}

.navbar-brand {
  font-weight: bold;
}

.venueMediaItem {
  width: 560px;
  height: 315px;
  max-width: 100%;
}

.center {
  text-align: center;
}

.markdown {
  text-align: center;
}

.markdown h1 {
  border-bottom: 4px solid #ffc107;
  display: inline-block;
  margin-bottom: 20px;
}

.markdown img {
  width: 50%;
}

/* Small devices (landscape phones, 576px and up) */
@media (max-width: 576px) {  

  .hero_h2 {
    font-size: 15px;
  }

  .hero_h3 {
    font-size: 15px;
  }

  .hero_h1 {
    font-size: 50px;
  }

  .hero_img {
    width: 90px;
  }

}
 
